import OpenIcon from "@material-ui/icons/Launch";
import MaterialTable from "material-table";
import React, { useEffect, useState } from "react";
import { useSelector } from "react-redux";
import { useHistory, Redirect } from "react-router-dom";
import { getUsers, deleteUser } from "../../redux/actions/users_actions";

const columns = [
  {
    title: " ",
    field: "userName",
  },
  {
    title: " ",
    field: "name",
  },
  {
    title: " ",
    field: "ownerName",
  },
  {
    title: "",
    field: "name",
  },
  {
    title: " ",
    field: "phoneNumber",
  },
  {
    title: "",
    field: "email",
  },
  {
    title: "",
    field: "role",
  },
];

const getRole = (role) => {
  switch (role) {
    case "admin":
      role = "";
      break;
    case "lab":
      role = " ";
      break;
    case "agency":
      role = "";
      break;
    case "user":
      role = "";
      break;
    default:
      break;
  }
};

export default function Users(props) {
  const history = useHistory();
  const role = useSelector((store) => store.authReducer.role);

  const [users, setUsers] = useState([]);

  useEffect(() => {
    const fetchData = async () => {
      if (role === "admin") {
        const result = await getUsers();
        const users = result.map((user) => ({
          ...user,
          role: getRole(user.role),
        }));
        setUsers(users);
      }
    };
    fetchData();
  }, [role]);

  return role === "admin" ? (
    <div style={{ margin: "50px", width: "75vw" }}>
      <MaterialTable
        title=""
        data={users}
        columns={columns}
        options={{
          exportButton: true,
          grouping: true,
          // selection: true,
          rowStyle: {
            backgroundColor: "#EEE",
          },
        }}
        localization={{
          toolbar: {
            searchPlaceholder: "",
            searchTooltip: "",
            exportTitle: " ",
            exportName: " ",
          },
          pagination: {
            labelDisplayedRows: " {from}  {to}    {count}",
            lastTooltip: "",
            firstTooltip: "",
            previousTooltip: "",
            nextTooltip: "",
            labelRowsSelect: "",
          },
          header: {
            actions: "",
          },
          body: {
            emptyDataSourceMessage: "  ",
            filterRow: {
              filterTooltip: "",
            },
            deleteTooltip: "",
            editTooltip: "",
          },
          grouping: {
            placeholder:
              "          ",
          },
        }}
        actions={[
          {
            icon: () => <OpenIcon></OpenIcon>,
            tooltip: "  ",
            onClick: (event, data) => {
              history.push(`/user/${data._id}`);
            },
          },
          {
            icon: "delete",
            tooltip: " ",
            onClick: async (event, data) => {
              await deleteUser(data._id).catch(() => {
                return;
              });
              setUsers(users.filter((user) => user._id !== data._id));
            },
          },
        ]}
      />
    </div>
  ) : (
    <Redirect to="/dashboard" />
  );
}
